import * as actionType from './actionType'
import { getData } from 'util'
import {
  API_PRODUCTCOSTSUMMARYS_PAGE,
  API_PRODUCTCOSTSUMMARYS_EXPORT
} from 'api'
//同步
export const update = object => {
  console.log('object is ', object)
  return {
    type: actionType.PRODUCTCOSTSUMMARY_UPDATE,
    data: object
  }
}

//异步
// 获取列表
export const handleGetList = (reqData = {}) => dispatch => {
  dispatch({
    type: actionType.PRODUCTCOSTSUMMARY_UPDATE,
    data: { tableLoading: true }
  })
  const { searchParams = {} } = reqData
  for (let i in searchParams) {
    if (Object.prototype.toString.call(searchParams[i]) === '[object Object]') {
      searchParams[i] = searchParams[i].value
    }
  }
  getData({ api: API_PRODUCTCOSTSUMMARYS_PAGE, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      let { page, size } = reqData.searchParams
      let { result, result: { total, list } } = data
      dispatch({
        type: actionType.PRODUCTCOSTSUMMARY_UPDATE,
        data: {
          page,
          size,
          tableLoading: false,
          total,
          searchResult: list || []
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.PRODUCTCOSTSUMMARY_FAIL
      })
    })
}
// 导出
export const handleExport = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.PRODUCTCOSTSUMMARY_UPDATE,
    data: { modalTuttonLoading: true }
  })
  getData({ api: API_PRODUCTCOSTSUMMARYS_EXPORT, ...reqData })
    .then(data => {
      callBack && callBack()
      dispatch({
        type: actionType.PRODUCTCOSTSUMMARY_UPDATE,
        data: { modalTuttonLoading: false }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.PRODUCTCOSTSUMMARY_FAIL
      })
    })
}



