import React, { PureComponent } from 'react'
import { Form, Button, Row, Col } from 'antd'
import style from './style/Form.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { CsxSelect, CsxSearch } from 'Form'
import {
  API_GET_PRODUCTION_CODEANDNAME,
  API_PRODUCTIONFREQUENCYS_PRODUCTIONLINE_FLITER_LIST,
  API_GET_LOCATION_CODEANDNAME
} from 'api'
@connect(
  state => ({ ...state.productCostSummary }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  // 改变当前的输入值
  onInputChange(values) {
    const { searchParams, update } = this.props
    update({ searchParams: { ...searchParams, ...values } })
  }
  // 查询数据
  query = () => {
    const { handleGetList, searchParams, size } = this.props
    let reqObj = { ...searchParams }
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        reqObj[i] = reqObj[i].value
      }
    }
    handleGetList({ searchParams: { ...reqObj, page: 1, size } })
  }

  // 重置
  reset = () => {
    const { update } = this.props
    update({ searchParams: {}, productCodeOptions: {} })
  }
  render() {
    const {
      searchParams = {},
      productCodeOptions,
      menuPermissionIds
    } = this.props
    const { lineCode, locationSearchKey } = searchParams
    return (
      <Form className={style.form}>
        <Row type='flex' justify='start'>
          <Col span={8}>
            <CsxSelect
              dateApi={API_PRODUCTIONFREQUENCYS_PRODUCTIONLINE_FLITER_LIST}
              label='产线编码'
              allowClear={true}
              selectItem={{
                data: lineCode,
                key: 'code',
                valueKey: ['code', 'name']
              }}
              onChangeSelect={value => {
                this.onInputChange({ lineCode: value })
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSearch
              dateApi={API_GET_PRODUCTION_CODEANDNAME}
              label='商品编码/名称'
              // showBoth={true}
              allowClear={true}
              searchParamsKey='product'
              selectItem={{
                data: productCodeOptions,
                key: 'code',
                valueKey: ['code', 'name']
              }}
              onChangeSelect={data => {
                const { value = '' } = data
                const { update } = this.props
                update({
                  searchParams: { ...searchParams, product: value },
                  productCodeOptions: data
                })
              }}
              onSelect={data => {
                const { update } = this.props
                const { value = '' } = data
                update({
                  searchParams: { ...searchParams, product: value },
                  productCodeOptions: data
                })
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSelect
              allowClear={true}
              dateApi={API_GET_LOCATION_CODEANDNAME}
              label='地点编码/名称'
              allowClear={true}
              selectItem={{
                data: locationSearchKey,
                key: 'code',
                valueKey: ['code', 'name']
              }}
              onChangeSelect={value => {
                console.log('value is', value)
                this.onInputChange({ locationSearchKey: value })
              }}
            />
          </Col>
          <Col span={8} push={16}>
            <Row type='flex' justify='end'>
              {menuPermissionIds &&
                menuPermissionIds.includes('productCostSummary_find') && (
                  <Button
                    type='primary'
                    className={style.mr10}
                    onClick={this.query}
                  >
                    查询
                  </Button>
                )}
              <Button type='primary' onClick={this.reset}>
                重置
              </Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



