import React, { PureComponent } from 'react'
import { message, Button } from 'antd'
import style from './style/Tool.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
@connect(
  state => ({ ...state.productCostSummary }),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  // 导出
  handleExport = () => {
    const { handleExport, searchParams, page, size } = this.props
    let reqObj = { ...searchParams }
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        reqObj[i] = reqObj[i].value
      }
    }
    handleExport({ searchParams: { page, size, ...reqObj } }, () => {
      message.success('导出成功')
    })
  }
  render() {
    const { modalTuttonLoading, menuPermissionIds = [] } = this.props
    return (
      <div className={style['table-tool']}>
        {menuPermissionIds && menuPermissionIds.includes('productCostSummary_export') && (
          <Button
            type='primary'
            className={style.btn}
            onClick={this.handleExport}
            loading={modalTuttonLoading}
          >
            导出
          </Button>
        )}
      </div>
    )
  }
}



